.nav-header {
  height: 50px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

& .navigation {
  display: inline-block;
  width: 50%;
  vertical-align: top;
  text-align: center;
  font: 500 16px Roboto,sans-serif;
  float: left;

  &__item {
    display: inline-block;
    margin: 14px 25px;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &-down {
      padding-left: 10px;
    }

  }

}

.location {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  font: 500 16px Roboto,sans-serif;
  float: left;

  &__item {
    display: inline-block;
    margin: 14px 0 14px 60px;
    color: #777;
    text-decoration: none;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &-down {
      padding-left: 10px;
    }

  }

}

.cart {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  font: 500 16px Roboto,sans-serif;

  &__item {
    display: inline-block;
    margin: 14px 60px 14px 0;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .2s;
    float: right;

    &:hover {
      color: #0D47A1;
    }

    &-circ {
      position: relative;
      top: -10px;
      padding: 3px;
      border-radius: 5px;
      font-size: 12px;
      color: #fff;
      background-color: #aadd12;
      transition: background .2s;
    }

    &:hover &-circ {
      background-color: #0D47A1;
    }

  }

}

.nav-container {
  width: 100%;

  &__title {
    display: inline-block;
    margin: 40px 0 40px 60px;
    font: 500 20px Roboto,sans-serif;
    text-transform: uppercase;
    color: #000;
  }

  &__btn {
    display: inline-block;
    margin: 30px 0 0 25px;
    font: 500 14px Roboto,sans-serif;
    color: #000;
    text-decoration: none;
    padding: 10px;
    border-radius: 5px;
    transition: background .2s;
    cursor: pointer;

    &:hover {
      background-color: #fff;
    }

    &_active {
      cursor: default;
      border: solid 1px #b8d6ed;
      background: #fff;
    }

  }

}


