.product-container {
  height: auto;

  &_ptr1 {
    height: 235px;
    width: 100%;
    background: url(../images/ptr1.png) repeat;
  }

  &_ptr2 {
    height: 235px;
    width: 100%;
    background: url(../images/ptr2.png) repeat;
  }

}

.product-table {
  width: 100%;
  height: 29px;
  padding-left: 30px;

  &_bd1 {
    display: inline-block;
    float: left;
    width:14px;
    height: 29px;
    background: url(../images/bd1.png) no-repeat;
  }

  &_bd2 {
    display: inline-block;
    float: left;
    width:calc(100% - 14px);
    height: 29px;
    background: url(../images/bd2.png) repeat;
  }

}

.product-wrapper {
  padding: 10px 40px;
}

.product-item {
  display: inline-block;
  position: relative;
  //padding: 20px;
  margin: 10px 10px 10px;
  border-radius: 5px;
  color: #000;
  transition: all .2s;
  max-width: 250px;
  height: 280px;

  &_result {
    background: #fff;
  }

  &:hover {
    background: #fff;
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  }

  &__img {
    padding-top: 20px;
    cursor: pointer;
    width: 250px;
    height: 250px;
  }

  &__price-header {
    position: absolute;
    top: 10px;
    right: 10px;
    background: #aadd12;
    padding: 6px 10px;
    border-radius: 0 5px 5px 0;
    font: 500 18px Roboto,sans-serif;
    color: #fff;
    white-space: nowrap;

    &_through {
      text-decoration: line-through;
      padding-right: 10px;
    }

    //&:before {
    //  content: '';
    //  position: absolute;
    //  top: 0;
    //  left: -34px;
    //  border: 17px solid transparent;	border-right: 17px solid #aadd12;
    //}

    & .arrow-left {
      display: inline-block;
      position: absolute;
      left: -17px;
      top: 0px;
      width: 0;
      height: 0;
      border-top: 18px solid transparent;
      border-bottom: 18px solid transparent;
      border-right: 18px solid #aadd12;
    }

    & .circle {
      width: 8px;
      height: 8px;
      background-color: #ffffff;
      position: absolute;
      border-radius: 50%;
      top: 14px;
      left: -7px;
    }

  }

  &__like {
    display: inline-block;
    position: absolute;
    top: 47px;
    right: 10px;
    padding: 3px 10px;
    font: 500 14px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    border-radius: 5px;

    & i {
      color: #fff;
      padding-right: 5px;
    }

  }

  &__add {
    position: absolute;
    bottom: 57px;
    right: 10px;
    color: #fff;
    background: #51c91c;
    width: 40px;
    height: 40px;
    font-size: 14px;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
    transition: all .2s;

    & i {
      position: relative;
      top: 13px;
    }

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      background: #43a617;
    }

  }

  &__price-footer{
    position: absolute;
    bottom: 50px;
    right: 60px;
    padding: 3px 10px;
    border-radius: 0 5px 5px 0;
    font: 500 14px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    white-space: nowrap;

    &_through {
      text-decoration: line-through;
      padding-right: 10px;
    }

    //&:before {
    //  content: '';
    //  position: absolute;
    //  top: 0;
    //  left: -34px;
    //  border: 17px solid transparent;	border-right: 17px solid #aadd12;
    //}

    & .arrow-left {
      display: inline-block;
      position: absolute;
      left: -14px;
      top: 0px;
      width: 0;
      height: 0;
      border-top: 11px solid transparent;
      border-bottom: 14px solid transparent;
      border-right: 14px solid #fec151;
    }

    & .circle {
      width: 5px;
      height: 5px;
      background-color: #ffffff;
      position: absolute;
      border-radius: 50%;
      top: 9px;
      left: -6px;
    }

  }

  &__dist {
    display: inline-block;
    position: absolute;
    bottom: 78px;
    right: 60px;
    padding: 3px 10px;
    font: 500 14px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    border-radius: 5px;

    & i {
      color: #fff;
      padding-right: 5px;
    }
  }

  &__title {
    position: absolute;
    bottom: 0;
    padding: 0 10px;
    text-align: center;
    font: 400 12px Roboto,sans-serif;
    color: #000;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 250px;
  }

}

